import './App.css';
import React, { useState, useEffect } from 'react';

import Blog from './components/blog.js'
import blogServices from './services/blogs.js';

function App() {

  const [blogs, setBlogs] = useState([]);

  useEffect( () => {

    blogServices
    .getAll()
    .then( allBlogs => {
      console.log(allBlogs)
      setBlogs(allBlogs)
    });

  }, []);

  return (
    <div id="wrapper">
      <h1>Blogs</h1>

      {
        blogs.map
        (
          blog =>
          <Blog key={blog.id} blog={blog} />
        )
      }

    </div>
  );
}

export default App;
